*{font-size:16px;}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #eee;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#main h1 {
  color: #444;
  text-align: center;
}

#main {
  padding: 0;
  box-sizing: border-box;
  width: 60%;
  height: 100%;
}

#book-list {
  padding: 0;
}
#book-list li {
  display: inline-block;
  margin: 1rem;
  padding: 1rem;
  border-radius: 1rem;
  border: .1rem solid #a62823;
  box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
  cursor: pointer;
  color: #a62823;
}

form {
  background: #fff;
  padding: 2rem;
  left: 0;
  position: fixed;
  bottom: 0;
  width: 60vw;
  box-sizing: border-box;
}

form .field {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
}

form label {
  text-align: right;
  padding: .5rem;
}

form select, form input {
  margin: .5rem;
  padding: .5rem;
  box-sizing: border-box;
}

form button {
  color: #fff;
  font-size: 3rem;
  background: #ad1457;
  border: 0;
  padding: 0 1rem;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
}

#book-details {
  position: fixed;
  top: 0;
  right: 0;
  width: 40vw;
  height: 100%;
  background: #a62823;
  overflow: auto;
  padding: 2rem;
  box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
  color: #fff;
  box-sizing: border-box;
}